/**
 * 
 * @param {VueComponent} context 
 * @param {string} componentName 
 * @returns {any} VueComponent 
 */
export function findComponentUpward(context, componentName) {
  let parent = context.$parent;
  let name = parent.$options.name;

  while (parent && (!name || [componentName].indexOf(name) < 0)) {
    parent = parent.$parent;
    if (parent) name = parent.$options.name;
  }
  return parent;
}

/**
 * 
 * @param {VueComponent} context 
 * @param {string} componentName 
 * @returns {array} Array<VueComponent>
 */
export function findComponentsUpward(context, componentName) {
  let parents = [];
  const parent = context.$parent;

  if (parent) {
    if (parent.$options.name === componentName) parents.push(parent);
    return parents.concat(findComponentsUpward(parent, componentName));
  } else {
    return [];
  }
}

/**
 * 
 * @param {VueComponent} context 
 * @param {string} componentName 
 * @returns {any} VueComponent 
 */
export function findComponentDownward(context, componentName) {
  const childrens = context.$children;
  let children = null;

  if (childrens.length) {
    for (const child of childrens) {
      const name = child.$options.name;

      if (name === componentName) {
        children = child;
        break;
      } else {
        children = findComponentDownward(child, componentName);
        if (children) break;
      }
    }
  }
  return children;
}

/**
 * 
 * @param {VueComponent} context 
 * @param {string} componentName 
 */
export function findComponentsDownward(context, componentName) {
  return context.$children.reduce((components, child) => {
    if (child.$options.name === componentName) components.push(child);
    const foundChilds = findComponentsDownward(child, componentName);
    return components.concat(foundChilds);
  }, []);
}


/**
 * 
 * @param {VueComponent} context 
 * @param {string} componentName 
 * @param {boolean} exceptMe 
 */
export function findBrothersComponents(context, componentName, exceptMe = true) {
  let res = context.$parent.$children.filter(item => {
    return item.$options.name === componentName;
  });
  let index = res.findIndex(item => item._uid === context._uid);
  if (exceptMe) res.splice(index, 1);
  return res;
}

/**
 * 
 * @param {number} min 
 * @param {number} max 
 */
export function randomNumber(min = 0, max = min + 1) {
  const { random, floor } = Math
  return floor(random() * (max - min + 1) + min)
}

/**
 * 
 * @param {number} digit 
 */
export function randomColor(digit = 6, unit) {
  return (unit || '') + Math.random().toString(16).slice(-digit);
}